.Collapsible{width:90%;font-family:"Rubik";font-size:2vmax}.Collapsible__contentInner{padding:10px;border-top:0}.Collapsible__contentInner p{margin-bottom:10px;font-size:14px;line-height:20px}.Collapsible__contentInner p:last-child{margin-bottom:0}.Collapsible__trigger{display:block;font-weight:400;text-decoration:none;position:relative;border:1px solid #363636;padding:10px;background:#111;color:#fff}.Collapsible__trigger:after{content:"⛛";position:absolute;right:10px;top:10px;display:block;transition:transform 300ms}.Collapsible__trigger.is-open:after{transform:rotateZ(180deg)}.Collapsible__trigger.is-disabled{opacity:.5;background-color:gray}