.ModalCollapsible{width:98%;font-family:"Rubik";font-size:2vmax}.ModalCollapsible__contentInner{border-top:0}.ModalCollapsible__contentInner p{margin-bottom:10px;font-size:14px;line-height:20px}.ModalCollapsible__contentInner p:last-child{margin-bottom:0}.ModalCollapsible__trigger{display:block;font-weight:400;text-decoration:none;position:relative;border:1px solid #363636;padding:10px;background:#111;color:#fff;transition:300ms}.ModalCollapsible__trigger:after{content:"✗";position:absolute;right:10px;top:10%;display:block;transition:transform 300ms}.ModalCollapsible__trigger.is-open{background:#505}.ModalCollapsible__trigger.is-open:after{content:"✓"}.ModalCollapsible__trigger.is-disabled{opacity:.5;background-color:gray}